import Vue from 'vue'
import XEUtils from 'xe-utils'
import {
  VXETable,
  Table,
  Column,
  Header,
  // Footer,

  Icon,
  // Filter,
  // Edit,
  // Menu,
  // Export,
  // Keyboard,
  // Validator,

  // Grid,
  // Toolbar,
  // Pager,
  Checkbox,
  Radio,
  Input,
  // Textarea,
  Button,
  Modal,
  // Tooltip,
  Form,
  // Select,
  Switch
  // List
} from 'vxe-table'

import zhCNLocat from 'vxe-table/lib/locale/lang/zh-CN'

// 导入默认的国际化（如果项目中使用多语言，则应该导入到 vue-i18n 中）
VXETable.setup({
  i18n: (key, args) => XEUtils.toFormatString(XEUtils.get(zhCNLocat, key), args)
})

// 全局默认参数
VXETable.setup({
  version: 0,
  zIndex: 100
})

// 表格模块
Vue.use(Icon)
Vue.use(Header)
// Vue.use(Footer)
// Vue.use(Filter)
// Vue.use(Edit)
// Vue.use(Menu)
// Vue.use(Export)
// Vue.use(Keyboard)
// Vue.use(Validator)

// 可选组件
Vue.use(Column)
// Vue.use(Grid)
// Vue.use(Tooltip)
// Vue.use(Toolbar)
// Vue.use(Pager)
Vue.use(Form)
Vue.use(Checkbox)
Vue.use(Radio)
Vue.use(Switch)
Vue.use(Input)
// Vue.use(Select)
Vue.use(Button)
Vue.use(Modal)
// Vue.use(List)

// 安装表格
Vue.use(Table)

Vue.prototype.$XModal = VXETable.modal
